/**
 * Utilitaires de s√©curit√© pour les endpoints CRON
 * V√©rification de secret, IP whitelist, logging
 */

import { NextRequest, NextResponse } from 'next/server'
import { logger } from './logger'

/**
 * Configuration de s√©curit√© CRON
 */
interface CronSecurityConfig {
  secret?: string // Secret partag√© (d√©faut: CRON_SECRET env var)
  allowedIPs?: string[] // Liste d'IPs autoris√©es (optionnel)
  requireSecret?: boolean // Exiger le secret (d√©faut: true)
  logExecution?: boolean // Logger les ex√©cutions (d√©faut: true)
}

/**
 * Valide la requ√™te CRON
 */
export function validateCronRequest(
  request: NextRequest,
  config: CronSecurityConfig = {}
): {
  valid: boolean
  error?: string
  details?: {
    secretValid: boolean
    ipValid: boolean
  }
} {
  const {
    secret = process.env.CRON_SECRET,
    allowedIPs = [],
    requireSecret = true,
  } = config

  const details = {
    secretValid: false,
    ipValid: false,
  }

  // 1. V√©rifier le secret
  if (requireSecret) {
    if (!secret) {
      return {
        valid: false,
        error: 'CRON_SECRET non configur√©',
        details,
      }
    }

    const authHeader = request.headers.get('authorization')
    const providedSecret = authHeader?.replace(/^Bearer\s+/i, '')

    if (!providedSecret) {
      return {
        valid: false,
        error: 'Secret manquant dans le header Authorization',
        details,
      }
    }

    details.secretValid = providedSecret === secret
    if (!details.secretValid) {
      return {
        valid: false,
        error: 'Secret invalide',
        details,
      }
    }
  } else {
    details.secretValid = true
  }

  // 2. V√©rifier l'IP (si whitelist configur√©e)
  if (allowedIPs.length > 0) {
    const forwarded = request.headers.get('x-forwarded-for')
    const realIp = request.headers.get('x-real-ip')
    const clientIp = forwarded?.split(',')[0]?.trim() || realIp || 'unknown'

    details.ipValid = allowedIPs.includes(clientIp)
    if (!details.ipValid) {
      return {
        valid: false,
        error: `IP non autoris√©e: ${clientIp}`,
        details,
      }
    }
  } else {
    details.ipValid = true
  }

  return {
    valid: true,
    details,
  }
}

/**
 * Middleware pour s√©curiser les endpoints CRON
 */
export async function withCronSecurity(
  request: NextRequest,
  handler: (req: NextRequest) => Promise<NextResponse>,
  config: CronSecurityConfig = {}
): Promise<NextResponse> {
  const { logExecution = true } = config

  // Logger la tentative d'ex√©cution
  if (logExecution) {
    const forwarded = request.headers.get('x-forwarded-for')
    const realIp = request.headers.get('x-real-ip')
    const clientIp = forwarded?.split(',')[0]?.trim() || realIp || 'unknown'
    const userAgent = request.headers.get('user-agent') || 'unknown'

    logger.info('CRON execution attempt', {
      ip: clientIp,
      userAgent,
      path: request.nextUrl.pathname,
      method: request.method,
    })
  }

  // Valider la requ√™te
  const validation = validateCronRequest(request, config)

  if (!validation.valid) {
    // Logger la tentative √©chou√©e
    if (logExecution) {
      logger.warn('CRON execution rejected', {
        error: validation.error,
        details: validation.details,
        path: request.nextUrl.pathname,
      })
    }

    return NextResponse.json(
      {
        error: validation.error || 'Unauthorized',
        success: false,
      },
      { status: 401 }
    )
  }

  // Ex√©cuter le handler
  return handler(request).then((response) => {
    // Logger l'ex√©cution r√©ussie
    if (logExecution) {
      logger.info('CRON execution completed', {
        path: request.nextUrl.pathname,
        status: response.status,
        success: response.ok,
      })
    }

    return response
  }).catch((error) => {
    // Logger l'erreur
    if (logExecution) {
      logger.error('CRON execution error', error, {
        path: request.nextUrl.pathname,
      })
    }

    throw error
  })
}



